import Header from "component/Header.jsx";
import SampleDB from "pages/hover/sample-db";
import SampleOood from "pages/hover/sample-oood";
import SampleLittleCake from "pages/hover/sample-littlecake";
import SampleFikon from "pages/hover/sample-fikon";
import SampleDimea from "pages/hover/sample-dimea";
import SampleWH from "pages/hover/sample-wh.jsx";
import SampleFZ from "pages/hover/sample-fz.jsx";

import { imagePaths } from "./Images.jsx";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import gsap from "gsap";

const Main = () => {

  // Hover
  const [sample, setSample] = useState(null);

  const handleMouseOver = (className) => {
    const samples = {
      "FungizZ": SampleFZ,
      "Whereisthehome": SampleWH,
      "digital-bookmark": SampleDB,
      "oood": SampleOood,
      "little-cake": SampleLittleCake,
      "fikon": SampleFikon,
      "dimea": SampleDimea,
    };
    const SampleComponent = samples[className];
    setSample(<SampleComponent />);
  };

  const handleMouseOut = () => {
    setSample(null);
  };


  // Random
  const [currentImage, setCurrentImage] = useState(null);

  const handleClick = () => {
    const randomImage = imagePaths[Math.floor(Math.random() * imagePaths.length)];
    setCurrentImage(randomImage);
  };

  const handleMouseEnter = () => {
    setCurrentImage(null);
  };

  // Animation
  useEffect(() => {
    gsap.to(".bounce", {
      x: 12,
      opacity: 1,
      duration: 1,
      ease: "ease",
    });

  }, []);

  return (
    <>
      {sample && <div className="sample-wrapper">{sample}</div>}
      <div className="main-wrapper">
        <div className="contents-wrapper">
          <div className="header-wrapper">
            {/* <Header className="header" /> */}
            <Header />
          </div>

          <div className="random-box">
            <div className="random-button">
              <div className=
                {'text-btn bounce ' + (currentImage ? 'text-active' : '')}
                onClick={handleClick}>
                <p class="random">(random)</p>
              </div>
            </div>

            <div className="random-image">
              {currentImage &&
                <Link to={currentImage.link}>
                  <img src={currentImage.src} alt="Random" />
                </Link>
              }
            </div>
          </div>

          <div className="table-box bounce" onMouseEnter={handleMouseEnter}>

            {/* <div className="t-head">
              <div className="head title">
                <p>title</p>
              </div>
              <div className="head year">
                <p>year</p>
              </div>
              <div className="head keyword">
                <p>keyword</p>
              </div>
            </div> */}

            <div className="t-body">
              <Link to="/project/fungizz">
                <div
                  className="contents fz"
                  onMouseOver={() => handleMouseOver("FungizZ")}
                  onMouseOut={handleMouseOut}
                >
                  <div className="content title-name">
                    <p>fungizZ</p>
                  </div>
                  <div className="content year-name">
                    <p>-</p>
                  </div>
                  <div className="content keyword-name">
                    <p>Graphic Design, Brand Design, Web Design</p>
                  </div>
                </div>
              </Link>
              <Link to="/project/whereisthehome">
                <div
                  className="contents wh"
                  onMouseOver={() => handleMouseOver("Whereisthehome")}
                  onMouseOut={handleMouseOut}
                >
                  <div className="content title-name">
                    <p>Where is the Home</p>
                  </div>
                  <div className="content year-name">
                    <p>2023</p>
                  </div>
                  <div className="content keyword-name">
                    <p>Web Design, Develop, 3D Works</p>
                  </div>
                </div>
              </Link>
              <Link to="/project/digital-bookmarks">
                <div
                  className="contents db"
                  onMouseOver={() => handleMouseOver("digital-bookmark")}
                  onMouseOut={handleMouseOut}
                >
                  <div className="content title-name">
                    <p>DIGITAL BOOKMARKS</p>
                  </div>
                  <div className="content year-name">
                    <p>2023</p>
                  </div>
                  <div className="content keyword-name">
                    <p>Web Design, Develop</p>
                  </div>
                </div>
              </Link>

              <Link to="/project/oood">
                <div
                  className="contents oood"
                  onMouseOver={() => handleMouseOver("oood")}
                  onMouseOut={handleMouseOut}
                >
                  <div className="content title-name">
                    <p>oood</p>
                  </div>
                  <div className="content year-name">
                    <p>2023</p>
                  </div>
                  <div className="content keyword-name">
                    <p>UI/UX Design, App Design, Social Media</p>
                  </div>
                </div>
              </Link>

              <Link to="/project/little-cake">
                <div
                  className="contents little-cake"
                  onMouseOver={() => handleMouseOver("little-cake")}
                  onMouseOut={handleMouseOut}
                >
                  <div className="content title-name">
                    <p>Little Cake</p>
                  </div>
                  <div className="content year-name">
                    <p>2022</p>
                  </div>
                  <div className="content keyword-name">
                    <p>Brand Design, Graphic Design, 3D Works</p>
                  </div>
                </div>
              </Link>

              <Link to="/project/fikon">
                <div className="contents fikon"
                  onMouseOver={() => handleMouseOver("fikon")}
                  onMouseOut={handleMouseOut}>
                  <div className="content title-name">
                    <p>fikon</p>
                  </div>
                  <div className="content year-name">
                    <p>2022</p>
                  </div>
                  <div className="content keyword-name">
                    <p>UI/UX Design</p>
                  </div>
                </div>
              </Link>

              <Link to="/project/dimea">
                <div
                  className="contents dimea"
                  onMouseOver={() => handleMouseOver("dimea")}
                  onMouseOut={handleMouseOut}
                >
                  <div className="content title-name">
                    <p>dimea</p>
                  </div>
                  <div className="content year-name">
                    <p>2022</p>
                  </div>
                  <div className="content keyword-name">
                    <p>Graphic Design, Brand Design, Web Design</p>
                  </div>
                </div>
              </Link>
            </div>
          </div>

          {/* <div className="bottom">
            <p>en / kr</p>
          </div> */}
        </div>
      </div>
    </>
  );
};

export default Main;
