import { BrowserRouter, Route, Routes } from "react-router-dom";
import Main from "pages/Main.jsx"
import Dimea from "pages/project/Dimea";
import Fikon from "pages/project/Fikon";
import LittleCake from "pages/project/LittleCake";
import Oood from "pages/project/Oood";
import DigitalBookmarks from "pages/project/DigitalBookmarks";
import WhereIsTheHome from "pages/project/WhereIsTheHome";
import FungizZ from "pages/project/FungizZ";

import Info from "pages/Info";
import Caw from "pages/Caw";

import "assets/css/style.css"

import Test from "pages/Test";

function App() {

  // Mobile
  function setScreenSize() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty("--vh", `${vh}px`); //"--vh"라는 속성으로 정의해준다.
  }

  window.addEventListener('resize', () => setScreenSize());

  return (
    <BrowserRouter>
      <Routes>
        {/* <Route path="/" element={<Home />} /> */}
        <Route path="/" element={<Main />} />
        <Route path="/test" element={<Test />} />
        <Route path="/project/dimea" element={<Dimea />} />
        <Route path="/project/fikon" element={<Fikon />} />
        <Route path="/project/little-cake" element={<LittleCake />} />
        <Route path="/project/oood" element={<Oood />} />
        <Route path="/project/digital-bookmarks" element={<DigitalBookmarks />} />
        <Route path="/project/whereisthehome" element={<WhereIsTheHome />} />
        <Route path="/project/fungizz" element={<FungizZ />} />

        <Route path="/info" element={<Info />} />
        <Route path="/caw" element={<Caw />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
