import Header from "component/Header";
import OpenSite from "assets/images/icons/open.png";

import Footer from "component/Footer";

import { useEffect, useState } from "react";

const Layout = (props) => {
  const [color, setColor] = useState('');
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 900);

  const handleResize = () => {
    setIsMobile(window.innerWidth <= 900);
  };

  // const videoProps = {
  //   className: "video",
  //   playsInline: true,
  //   loop: true,
  //   muted: "muted",
  //   ...(!isMobile && { autoPlay: true }), 
  // };

  useEffect(() => {
    const changeColor = () => {
      if (window.scrollY >= 80) {
        setColor(true);
      } else {
        setColor(false);
      }
    };
    window.addEventListener('resize', handleResize);
    window.addEventListener("scroll", changeColor);

    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener("scroll", changeColor);
    };
  }, []);

  return (
    <>
      <div className="layout-wrapper">
        <div className="contents-wrapper">
          <div className={'header-box layout ' + (color ? 'header-active' : 'header-notactive')}>
            <Header />
          </div>

          <div className="contents-container">
            <div className="contents-box">
              <div className="info-box">
                <div className="title-box">
                  <div className="title">{props.title}</div>
                  <div className="keyword">
                    <p>{props.keyword1}</p>
                    <p>{props.keyword2}</p>
                  </div>
                </div>

                <div className={`button-box ${props.openVisibility}`}>

                  <a href={props.site} target="_blank" rel="noreferrer">
                    Open
                  </a>
                  <img src={OpenSite} alt="arrow" />

                </div>
              </div>

              <div className="image-box">
                {props.contentArray.map((content, index) => {
                  if (content.type === "image") {
                    return (
                      <img
                        key={index}
                        src={content.src}
                        alt={`Dimea${index + 1}`}
                      />
                    );
                  } else if (content.type === "iframe") {
                    return (
                      <>
                        <div className="iframe-wrapper">
                          <iframe
                            key={index}
                            src={content.src}
                            allow="autoPlay; allowFullScreen; picture-in-picture"
                            allowFullScreen
                            autoPlay="1"
                            loop="1"
                            autopause="0"
                            style={{
                              position: "absolute",
                              top: 0,
                              left: 0,
                              width: "100%",
                              height: "100%",
                            }}
                          ></iframe>
                        </div>
                        <script src="https://player.vimeo.com/api/player.js"></script>
                      </>
                    );
                  } else if (content.type === "video") {
                    return (
                      <>
                        <video
                          playsInline
                          key={index}
                          className="video"
                          autoplay={isMobile ? '' : "true"}
                          loop
                          muted="muted"
                          src={content.src}
                        >
                          {/* <video {...videoProps}> */}
                          {/* <source src={content.src} /> */}
                        </video>
                      </>
                    );
                  }
                  return null;
                })}
              </div>
              <Footer />
            </div>
          </div>

        </div>
      </div>
    </>
  );
};

export default Layout;
