import React from "react";
import Header from "component/Header";
import "helper/p5sound_fix.js";
import "p5/lib/addons/p5.sound";
import * as p5 from "p5";

import Sound1 from "assets/sounds/2.mp3"
import Sound2 from "assets/sounds/1.mp3"
import Sound3 from "assets/sounds/4.mp3"
import Sound4 from "assets/sounds/3.mp3"
import Sound5 from "assets/sounds/5.mp3"

class Caw extends React.Component {
  // React ref for div reference we pass into p5
  constructor() {
    super()
    this.sketchRef = React.createRef();
  }

  // sketchRef = React.createRef();

  // P5 instance mode
  initSketch = (p) => {
    let canvas;
    let w = this.sketchRef.current.clientWidth;
    let h = this.sketchRef.current.clientHeight;

    // Sound
    let s1;
    let s2;
    let s3;
    let s4;
    let s5;

    // Button
    let button1;
    let button2;
    let button3;
    let button4;
    let button5;

    // Button Container
    let buttonContainer;

    let amp;
    let vol;
    let df = 1;

    // Color
    let circleColor;

    p.preload = () => {

      // Preload Sound
      p.soundFormats('mp3');
      s1 = p.loadSound(Sound1);
      s2 = p.loadSound(Sound2);
      s3 = p.loadSound(Sound3);
      s4 = p.loadSound(Sound4);
      s5 = p.loadSound(Sound5);

    };

    p.setup = () => {
      canvas = p.createCanvas(w, h);
      amp = new p5.Amplitude();

      // Making buttons
      button1 = p.createButton("C7");
      button2 = p.createButton("C♯M7");
      button3 = p.createButton("D♯m7 ");
      button4 = p.createButton("Fm7");
      button5 = p.createButton("G♯7");


      button1.mousePressed(p.s1Playing);
      button2.mousePressed(p.s2Playing);
      button3.mousePressed(p.s3Playing);
      button4.mousePressed(p.s4Playing);
      button5.mousePressed(p.s5Playing);


      // Add Class on Button
      button1.class('chord');
      button2.class('chord');
      button3.class('chord');
      button4.class('chord');
      button5.class('chord');

      // Button Container
      let buttonContainer = p.createDiv();
      buttonContainer.class('button-container');

      // Add Button into Container

      button1.parent(buttonContainer);
      button2.parent(buttonContainer);
      button3.parent(buttonContainer);
      button4.parent(buttonContainer);
      button5.parent(buttonContainer);

      circleColor = "#000000";


    };


    p.s1Playing = () => {

      s1.play();
      circleColor = "#F1C9FF";

      s1.onended(() => {
        circleColor = "#000000";
      });
    }

    p.s2Playing = () => {
      s2.play();
      circleColor = "#F1C9FF";

      s2.onended(() => {
        circleColor = "#000000";
      });
    }

    p.s3Playing = () => {
      s3.play();
      circleColor = "#F1C9FF";
      
      s3.onended(() => {
        circleColor = "#000000";
      });
    }

    p.s4Playing = () => {
      s4.play();
      circleColor = "#F1C9FF";
      
      s4.onended(() => {
        circleColor = "#000000";
      });
    }

    p.s5Playing = () => {
      s5.play();
      circleColor = "#F1C9FF";
      
      s5.onended(() => {
        circleColor = "#000000";
      });
    }

    p.draw = () => {

      let vol = amp.getLevel();
      let diam = p.map(vol, 0, 0.3, 0, 80);

      // let plus = diam *

      p.background("#ffffff");
      let els = 16;
      let elw = w / els;
      // let step = 360 / els;
      p.push();
      p.translate(elw / 2, h / 2);
      p.fill(circleColor);
      let magnitude = h * 0.01;
      let s = 10;
      p.noStroke();

      for (let i = 0; i < els; i++) {
        p.push();
        let wave = p.map(p.sin(p.radians(p.frameCount + i * 20)), -1, 1, -magnitude, magnitude);
        
        p.translate(i * elw, wave * df);
        p.ellipse(0, 0, s + diam, s + diam * 8);
        p.pop();
      }
      p.pop();

    };


    p.windowResized = () => {
      const element = document.getElementById("canvas-id");
      if (!element || element.clientWidth === 0) return;

      w = this.sketchRef.current.clientWidth;
      h = this.sketchRef.current.clientHeight;
      p.resizeCanvas(w, h);
    };
  };

  componentDidMount() {
    //Create p5 canvas once component mounts
    this.p5Canvas = new p5(this.initSketch, this.sketchRef.current);
  }

  componentWillUnmount() {
    this.p5Canvas.remove();
    // this.p5Canvas = new p5(this.initSketch, this.sketchRef.current)
    window.removeEventListener("resize", this.windowResized);
  }

  render() {
    //this div will contain the sketch canvas
    return (
      <div className="sub-wrapper">
        <div className="contents-wrapper caw">
          <div className="header-wrapper">
            <Header />
          </div>
          <div className="caw-wrapper">
            <div id="canvas-id" className="canvas-wrapper" ref={this.sketchRef}></div>
          </div>

        </div>
      </div>
    );
  }
}

export default Caw;
