import { Link, useLocation } from "react-router-dom";
const Header = (props) => {
  const location = useLocation();

  const getMenuNumber = (pathname, target) => {
    if (pathname === "/" && target === "project") {
      return "●";
    } else if (pathname.includes(`/${target}`) && target !== "") {
      return "●";
    } else {
      return "";
    }
  };

  return (
    <div className="header-box">
      <Link to="/">
        <div className={`logo-box ${props.className}`}>
          <div className="name">Korp</div>
        </div>
      </Link>

      <div className="main-menu items">
        <Link to="/">
          <p className={`item ${getMenuNumber(location.pathname, "project") === "●" ? "here" : ""}`}>
            [ <span>{getMenuNumber(location.pathname, "project")}</span> ] Project
          </p>
        </Link>
        <Link to="/info">
          <p className={`item ${getMenuNumber(location.pathname, "info") === "●" ? "here" : ""}`}>
            [ <span>{getMenuNumber(location.pathname, "info")}</span> ] Info
          </p>
        </Link>
        {/* <Link to="/twinkling">
        <p className={`item ${getMenuNumber(location.pathname, "twinkling") === "●" ? "here" : ""}`}>
          [ <span>{getMenuNumber(location.pathname, "twinkling")}</span> ] Twinkling
        </p>
      </Link> */}
        <Link to="/caw">
          <p className={`item ${getMenuNumber(location.pathname, "caw") === "●" ? "here" : ""}`}>
            [ <span>{getMenuNumber(location.pathname, "caw")}</span> ] Caw
          </p>
        </Link>
      </div>
    </div>
  );
};

export default Header;
